.App {
  text-align: center;
  padding: 20px;
}

.movie-list {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-around;
}

.movie-card {
  border: 1px solid #ccc;
  padding: 10px;
  margin: 10px;
  max-width: 300px;
}

.movie-card img {
  width: 100%;
}

.movie-summary {
  text-align: left;
  padding: 20px;
  border: 1px solid #ccc;
  margin-top: 20px;
}

li {
  list-style: url(https://th.bing.com/th?id=OIP.SyxyBIEVZqSemLL2gPH3BwHaEn&w=15&h=15&c=8&rs=1&qlt=90&o=6&dpr=1.6&pid=3.1&rm=2);
  margin: 5px 0;
  cursor: pointer;
  color: #333;
  transition: color 0.3s;
}
li:hover {
  color: #007bff;
}
.movie-description {
  font-size: 14px;
  margin-bottom: 10px;
}
.input {
  border: 1px solid #ccc;
  border-radius: 4px;
  font-size: 14px;
  margin-left: 5px;
}
#main{
  display: flex;
  flex-direction: row;
}
button {
  background-color: #ec4747;
  color: #fff;
  border: none;
  cursor: pointer;
  font-size: 14px;
  transition: background-color 0.3s;
  border-radius: 5px ;
}
button:hover {
  background-color: #964f5d;
}
h2{
  color:#ec4747;
}
h3{
  color: #007bff;
}

img{
  border-radius: 5px 5px 5px  5px;
}
.main{
  display: flex;
}
.sub{
  margin-left: 20px;
}
input{
  margin-left: 5px;
}
.form{
  margin-bottom: 30px;
}






